/* General Page Styles */
body {
    font-family: "Arial", sans-serif;
    background-color: #f4f4f4;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
  }
  
  /* Main Container */
  .container {
    width: 80%;
    max-width: 900px;
    background: white;
    padding: 20px;
    margin-top: 30px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  /* Title */
  .title {
    font-size: 28px;
    font-weight: bold;
    color: #333;
    margin-bottom: 15px;
  }
  
  /* Filter Buttons */
  .filter-section {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 20px;
  }
  
  .filter-btn {
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    background: #007bff;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .filter-btn:hover {
    background: #0056b3;
  }
  
  /* Cluster Cards */
  .clusters {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 15px;
    margin-top: 20px;
  }
  
  .cluster-card {
    background: #fff;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    transition: 0.3s;
  }
  
  .cluster-card h3 {
    margin: 0;
    color: #333;
  }
  
  .cluster-card p {
    font-size: 16px;
    font-weight: bold;
    color: #007bff;
  }
  
  /* Summary Section */
  .summary {
    margin-top: 30px;
    padding: 15px;
    border-top: 2px solid #ddd;
  }
  
  .summary h2 {
    color: #333;
  }
  
  .summary ul {
    list-style-type: none;
    padding: 0;
  }
  
  .summary li {
    font-size: 16px;
    margin: 5px 0;
  }
  